@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
@import url(https://fonts.googleapis.com/css?family=El+Messiri|Roboto);
body, html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
     height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
The CSS for the ENTIRE WEBPAGE. Basically.

Since our app was made with React, we can get away with using this one
gigantic monster of a CSS file to stylize the whole page.
*/



/* Import google fonts. */



/* Some elements will appear / disappear at smaller screen sizes. */
h1 {
  font-size: 36pt;
}

h2 {
  font-size: 24pt;
}

#landing-footer {
  margin-top: 10px;
}

.show-sm {
  display: none;
}

@media (max-width: 575px) {
  .hide-sm {
    display: none;
  }

  .show-sm {
    display: inline;
    display: initial;
  }
}

@media (min-width: 576px) {
  h1 {
    font-size: 48pt;
  }

  h2 {
    font-size: 36pt;
  }

  #landing-footer {
    margin-top: 50px;
  }
}

@media (min-width: 768px) {
  h1 {
    font-size: 64pt;
  }

  h2 {
    font-size: 40pt;
  }
}

/* End screen-size based area */



h3 {
  font-size: 24pt;
}

html {
   min-height: 100%;
   box-sizing: border-box;
   padding-bottom: 20px;

}

.font-el {
    font-family: 'El Messiri', sans-serif;
}

.font-el2 {
    font-family: 'El Messiri', sans-serif;
    color: black;
}

.text-center{
    height: 100%;
}

.font-ro {
    font-family: 'Roboto', sans-serif;
}

.text-on-background-lg {
    -webkit-text-stroke: 1px #000000;
}

.text-on-background-sm {
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

#main-display {
    background-image: url(/static/media/forestbackground.f4f751a6.jpg);
    background-color: #999999;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

#landing-navigate {
    text-align: center;
    padding-top: 20px;
    margin-top: 30px;
    background-color: #6C9661;
    border-radius: 20px;
    box-shadow: 6px 6px 18px black;
}

/* appear - on page load */
.fade-appear {
    opacity: 0;
    z-index: 1;
}
.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 5000ms linear;
}
.fade-exit-done {
  opacity: 0;
}

.navbar {
  background-color: #30572D;
}
.person-mug {
    border-radius: 200px;
}

.nav-option {
    margin-bottom: 2px;
    margin-right: 5px;
    margin-left: 5px;
    width: 125px;
}

#holder {
    padding-bottom: 50px;
}

#main-content {
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 6px 6px 18px black;
    padding-top: 10px;
    padding-bottom: 10px;
}

#mobile-nav-button {
    background-color: #6C9661;
}

.Logo_Image{
  width:120px
}

#logo{
  width:350px;
}

.Mug_Image{
  border-radius: 200px;
  height:200px;
  width:200px;
  display: block;
  margin: 0 auto;
}
.news-item {
    margin-bottom: 10px;
    margin-top: 25px;
}
*, *:before, *:after {
    box-sizing: border-box;
}
@-webkit-keyframes AnimationName {
  0%{background-position:0px 0px}

  100%{background-position:0px 450px}

}
@keyframes AnimationName {
  0%{background-position:0px 0px}

  100%{background-position:0px 450px}

}

body {
  background-color: rgb(108, 150, 97);
  background-image: url(/static/media/Leaf.a1286abc.png);
  background-position: center center;
  background-size: 500px;
  background-repeat: repeat;
  -webkit-animation: AnimationName 4s linear infinite;
          animation: AnimationName 4s linear infinite;
}

.panel {
  padding: 3rem;
  height: 100%;

}
.contain {
  padding-top: 50px;
}

.about_person {
  height: 100%;
}

h1 {
  color: white;
  font-weight: 400;
  font-family: sans-serif;
  margin: 0;
  position: relative;
  z-index: 2;
}
.col2{
  max-width: 33.33%;
}

.card-sm {
  height: 500px;
  overflow: hidden;
}

.card-img-top {
  object-fit: cover;
  height: 400px;
}

.card-img-top-sm {
  object-fit: cover;
  height: 175px;
}

.trim {
  overflow: hidden;
  height: 100px;
}
@-webkit-keyframes fade-in {
0%{
  opacity: 0;
  -webkit-transform: translate3d(0, 50px, 0);
          transform: translate3d(0, 50px, 0);
}
100%{
  opacity: 1;
  -webkit-transform: translate3d(0, 0px, 0);
          transform: translate3d(0, 0px, 0);
}
}
@keyframes fade-in {
0%{
  opacity: 0;
  -webkit-transform: translate3d(0, 50px, 0);
          transform: translate3d(0, 50px, 0);
}
100%{
  opacity: 1;
  -webkit-transform: translate3d(0, 0px, 0);
          transform: translate3d(0, 0px, 0);
}
}
.news-item{
  -webkit-animation: 1s ease-out 0s 1 fade-in;
          animation: 1s ease-out 0s 1 fade-in;
}

.hideBar {
  overflow: hidden;
}
